import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { forkJoin, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class ShoesService {
  // corsProxyServer = "https://thingproxy.freeboard.io/fetch";
  corsProxyServer = "https://digoro-cors-anywhere.herokuapp.com";

  constructor(
    private http: HttpClient,
  ) { }

  getItems() {
    let items = JSON.parse(localStorage.getItem('items'));
    items = items ? items : [];
    return items;
  }

  addItem(id) {
    const items = this.getItems();
    if (items.find(item => item === id)) {
      alert('이미 등록되어 있습니다.')
    } else {
      localStorage.setItem('items', JSON.stringify([...items, id]));
    }
  }

  removeItem(id) {
    let items = this.getItems() as any[];
    if (items.length === 1) {
      localStorage.setItem('items', JSON.stringify([]))
    } else {
      localStorage.removeItem('items');
      items = items.filter(item => item !== id)
      localStorage.setItem('items', JSON.stringify(items))
    }
  }

  removeAll() {
    localStorage.removeItem('items');
  }

  getShoeInfo(prdNo: number) {
    return this.http.get(`${this.corsProxyServer}/http://abcmart.a-rt.com/product/info?prdtNo=${prdNo}`).pipe(
      map(response => this.mapData(response, prdNo))
    )
  }

  private mapData(response, prdNo: number) {
    const data = response;
    if (data) {
      return {
        status: true,
        data: data
      };
    }
    else {
      return {
        status: false,
        data: prdNo
      }
    }
  }

  getShoesInfo(prdNoList: number[]) {
    const requests: Observable<any>[] = [];
    prdNoList.map(prdNo => {
      return requests.push(this.http.get(`${this.corsProxyServer}/http://abcmart.a-rt.com/product/info?prdtNo=${prdNo}`).pipe(
        map(response => this.mapData(response, prdNo))
      ))
    });
    if (requests.length === 0) return of([]);
    return forkJoin(requests);
  }
}
